.message-modal .header {
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e9e9e9;
}

.message-modal .body {
    padding-top: 15px;
    overflow-y: auto;
}

.message-modal .rodal-dialog {
    height: 500px!important;
    width: 600px!important;
}

@media screen and (max-width: 992px) {
    .message-modal .rodal-dialog {
        height: 96%!important;
        width: 96%!important;
    }
}

.message-modal .rodal-cancel-btn,
.message-modal .rodal-confirm-btn {
    position: absolute;
    font: inherit;
    bottom: 16px;
    width: 70px;
    padding: 4px 15px;
    border-radius: 3px;
    transition: background 0.2s;
    border: 1px solid var(--color__100grammes);
}

.message-modal .rodal-confirm-btn {
    color: #fff;
    right: 20px;
    background: var(--color__100grammes);
    cursor: pointer;
    width: 30%;
    border-radius: 45px;
    height: 4rem;
}

.message-modal .rodal-confirm-btn:hover {
    background: var(--color__100grammes);
}

.message-modal .rodal-cancel-btn {
    color: var(--color__100grammes);
    right: 16px;
    background: #fff;
    cursor: pointer;
}

.message-modal .rodal-cancel-btn:hover {
    background: #fafafa;
}

.message-modal .rodal-cancel-btn:focus,
.message-modal .rodal-confirm-btn:focus {
    outline: none;
}

.message-modal .rodal-confirm-btn:active {
    background: var(--color__100grammes);
}

.message-modal .rodal-cancel-btn:active {
    background: #fafafa;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px transparent;
}