/*--------------------------------------------------------------
>>> TABLE OF CONTENTS:
----------------------------------------------------------------*/

:root {
    --color__black: #000000;
    --color__white: #FFFFFF;
    --color__lightGreen: #27B090;
    --font-dosis: "Dosis", sans-serif;
    --font-pacifico: "Pacifico", cursive;
    --iga: #f99200;
    --maxi: #05ab63;
    --metro: #c20505;
    --provigo: #01446f;
    --superc: #6dc5fd;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 58%;
    -webkit-text-size-adjust: 100%;
}

@media screen and (min-width: 768px) {
    html {
        font-size: 60%;
    }
}

@media screen and (min-width: 992px) {
    html {
        font-size: 62.5%;
    }
}

body {
    font-size: 1.6rem;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: var(--font-dosis);
}

.apps_wrapper p {
    font-size: 1.6rem;
    line-height: 1.5;
    margin: 0;
}

.apps_wrapper img {
    max-width: 100%;
    height: auto;
}

.apps_wrapper ul,
.apps_wrapper ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

.apps_wrapper ul li,
.apps_wrapper ol li {
    font-size: 1.6rem;
}

.apps_wrapper a {
    color: inherit;
    text-decoration: none;
    font-size: 1.6rem;
}

.apps_wrapper a:hover {
    color: inherit;
    text-decoration: none;
}

.apps_wrapper h1,
.apps_wrapper h2,
.apps_wrapper h3,
.apps_wrapper h4,
.apps_wrapper h5,
.apps_wrapper h6 {
    margin: 0;
    padding: 0;
}

.apps_wrapper button {
    border: 0;
    background-color: transparent;
    margin: 0;
    padding: 0;
}

.apps_wrapper .font-dosis {
    font-family: var(--font-dosis);
}

.apps_wrapper .font-pacifico {
    font-family: var(--font-pacifico);
}

.apps_wrapper .text-white {
    color: white;
}

.apps_wrapper .text-center {
    text-align: center;
}


/*-------------------------------------------------
      [ ## Fields ]
  ---------------------------------------------------*/

.apps_wrapper input[type=text],
.apps_wrapper input[type=email],
.apps_wrapper input[type=url],
.apps_wrapper input[type=password],
.apps_wrapper input[type=number],
.apps_wrapper input[type=search] {
    padding: 1rem 1.5rem;
    font-size: 1.6rem;
    font-family: var(--font-dosis);
}

.apps_wrapper input[type=text]:focus,
.apps_wrapper input[type=email]:focus,
.apps_wrapper input[type=url]:focus,
.apps_wrapper input[type=password]:focus,
.apps_wrapper input[type=number]:focus,
.apps_wrapper input[type=search]:focus {
    outline: none;
    box-shadow: none;
    border-color: #ced4da;
}

.apps_wrapper input[type=time]::-webkit-inner-spin-button,
.apps_wrapper input[type=time]::-webkit-outer-spin-button,
.apps_wrapper input[type=date]::-webkit-inner-spin-button,
.apps_wrapper input[type=date]::-webkit-outer-spin-button,
.apps_wrapper input[type=number]::-webkit-inner-spin-button,
.apps_wrapper input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.apps_wrapper input[type=search] {
    -webkit-appearance: textfield;
}

.apps_wrapper input[type=search]::-webkit-search-decoration {
    -webkit-appearance: none;
}

.apps_wrapper select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    font-family: var(--font-dosis);
    padding: 1rem 1.5rem;
    font-size: 1.6rem;
}

.apps_wrapper input:-webkit-autofill,
.apps_wrapper textarea:-webkit-autofill,
.apps_wrapper select:-webkit-autofill {
    box-shadow: 0 0 0px 0 rgba(var(--color__black), 0) inset;
    -webkit-box-shadow: 0 0 0px 0 rgba(var(--color__black), 0) inset;
    -webkit-text-fill-color: var(--color__black) !important;
}

.apps_wrapper input:-webkit-autofill:focus {
    box-shadow: 0 0 0px 0 rgba(var(--color__black), 0) inset;
    -webkit-box-shadow: 0 0 0px 0 rgba(var(--color__black), 0) inset;
    -webkit-text-fill-color: var(--color__black) !important;
}

.apps_wrapper textarea.form-control {
    display: block;
    width: 100%;
    display: block;
    min-height: 150px;
    font-family: var(--font-dosis);
    padding: 1rem 1.5rem;
    font-size: 1.6rem;
}

.apps_wrapper textarea.form-control:focus {
    outline: none;
    box-shadow: none;
}

.apps_wrapper .form-floating>.form-control {
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0;
}

 ::-webkit-input-placeholder {
    color: var(--color__black);
}

.apps_wrapper .widget_newsletter::-webkit-input-placeholder {
    color: #808080;
}

 :-moz-placeholder {
    color: var(--color__black);
    opacity: 1;
}

 ::-moz-placeholder {
    color: var(--color__black);
    opacity: 1;
}

 :-ms-input-placeholder {
    color: var(--color__black);
}

.apps_wrapper .text-red {
    color: red;
}

.apps_wrapper .text-center {
    text-align: center;
}

.apps_wrapper .breadcrumb {
    justify-content: center;
    margin: 0;
}

.apps_wrapper .breadcrumb .breadcrumb-item a {
    color: var(--color__white);
    font-size: 1rem;
}

@media screen and (min-width: 576px) {
    .apps_wrapper .breadcrumb .breadcrumb-item a {
        font-size: 1.2rem;
    }
}

@media screen and (min-width: 768px) {
    .apps_wrapper .breadcrumb .breadcrumb-item a {
        font-size: 1.5rem;
    }
}

.apps_wrapper .breadcrumb .breadcrumb-item+.breadcrumb-item::before {
    content: "-";
    color: var(--color__white);
    position: relative;
    top: 6px;
}

.apps_wrapper .topbutton {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    background-color: var(--color__white);
    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
    font-size: 2rem;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    border-radius: 0.5rem;
    opacity: 0;
    visibility: hidden;
    z-index: 9999;
}

.apps_wrapper .topbutton:hover {
    color: var(--color__white);
    background-color: var(--color__primary);
}

.apps_wrapper .topbutton.btn-show {
    visibility: visible;
    opacity: 1;
    bottom: 4rem;
    right: 4rem;
}

.apps_wrapper .btn-theme {
    background-color: var(--color__primary);
    color: var(--color__white);
    border-radius: 0.8rem;
    padding: 1.5rem 3rem;
    font-size: 1.6rem;
    font-weight: 600;
    transition: all 0.3s;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.apps_wrapper .btn-theme:hover {
    color: var(--color__white);
}

.apps_wrapper .btn-rounded-white {
    background-color: var(--color__white);
    color: var(--color__primary);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
    text-align: center;
}

.apps_wrapper .btn-theme-primary {
    background-color: var(--color__primary);
    color: var(--color__white);
    border-radius: 0.4rem;
    padding: 1.5rem 3rem;
    display: inline-block;
    transition: all 0.3s;
}

.apps_wrapper .btn-theme-primary:hover {
    color: var(--color__white);
}

.apps_wrapper .btn-rounded-primary {
    background-color: var(--color__primary);
    color: var(--color__white);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
    text-align: center;
}

.apps_wrapper .btn-rounded-primary:hover {
    color: var(--color__white);
}

.apps_wrapper .btn-rounded-primary-outline {
    background-color: transparent;
    color: var(--color__primary);
    border: 0.1rem solid var(--color__primary);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
}

.apps_wrapper .btn-rounded-primary-outline svg,
.apps_wrapper .btn-rounded-primary-outline svg path {
    transition: all 0.3s;
}

.apps_wrapper .btn-rounded-primary-outline:hover {
    background-color: var(--color__primary);
    color: var(--color__white);
}

.apps_wrapper .btn-rounded-primary-outline:hover svg path {
    fill: #fff;
    stroke: #fff;
}

.apps_wrapper .btn-circle-white {
    background-color: var(--color__white);
    color: var(--color__primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4.5rem;
    height: 4.5rem;
    transition: all 0.3s;
}

.apps_wrapper .btn-circle-primary {
    background-color: var(--color__primary);
    color: var(--color__white);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4.5rem;
    height: 4.5rem;
    transition: all 0.3s;
}

@media screen and (min-width: 576px) {
    .modal.user_agreement .modal-dialog {
        min-width: 60rem;
    }
}

.modal.user_agreement .modal-body {
    padding: 5rem 4rem 2rem 5rem;
}

@media screen and (max-width: 575px) {
    .modal.user_agreement .modal-body {
        padding: 3rem 2rem 2rem 2rem;
    }
}

.modal.user_agreement .modal-footer {
    border: 0;
    padding: 1rem 5rem 5rem;
}

@media screen and (max-width: 575px) {
    .modal.user_agreement .modal-footer {
        padding: 1rem 2rem 3rem;
    }
}

.modal.user_agreement .modal-footer button {
    font-size: 1.6rem;
    padding: 1rem 3rem;
    margin: 0 1rem;
}

.modal.user_agreement .modal-title {
    margin-bottom: 1rem;
    color: var(--color__primary);
    font-size: 2rem;
}

.modal.user_agreement .agreements_details {
    max-height: 35rem;
    overflow-y: scroll;
    padding-right: 1rem;
}

.modal.user_agreement .agreements_details::-webkit-scrollbar {
    width: 0.5rem;
    background-color: #EEEEEE;
}

.modal.user_agreement .agreements_details::-webkit-scrollbar-thumb {
    background-color: var(--color__primary);
}

.modal.user_agreement .agreements_details ol {
    counter-reset: cupcake;
}

.modal.user_agreement .agreements_details ol li {
    counter-increment: cupcake;
    padding-left: 2rem;
}

.modal.user_agreement .agreements_details ol li:before {
    content: counters(cupcake, ". ") ". ";
    margin-left: -1.8rem;
}

.modal.user_agreement .agreements_details ol li+li {
    margin-top: 2rem;
}

@media screen and (min-width: 576px) {
    .modal.update-password .modal-dialog {
        min-width: 60rem;
    }
}

.modal.update-password .btn-close {
    width: 4rem;
    height: 4rem;
    border: 0.1rem solid var(--color__theme__black);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color__theme__black);
    opacity: 1;
    padding: 0;
    position: absolute;
    right: -2rem;
    top: -2rem;
    background-color: var(--color__white);
    font-size: 1.2rem;
    outline: 0;
    box-shadow: none;
    z-index: 9;
}

.modal.update-password .modal-content {
    border-radius: 1rem;
}

.modal.update-password .modal-body {
    padding: 5rem;
}

.modal.update-password .modal-title {
    margin: 1rem 0;
    color: var(--color__theme__black);
    font-size: 3rem;
    font-weight: bold;
}

.modal.recharge_success .modal-content {
    border-radius: 1rem;
}

.modal.recharge_success .modal-body {
    padding: 6rem 4rem;
}

.modal.recharge_success h6 {
    font-size: 2rem;
}

.modal.recharge_success h3 {
    font-size: 4rem;
}

.tooltip {
    --bs-tooltip-arrow-width: 1.3rem;
    --bs-tooltip-arrow-height: .8rem;
}

.tooltip .tooltip-inner {
    font-size: 1.3rem;
    padding: 0.5rem;
}

@keyframes spin {
    0% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    10% {
        transform: translate(-1px, -3px) rotate(36deg);
    }
    20% {
        transform: translate(-2px, 0px) rotate(72deg);
    }
    30% {
        transform: translate(1px, 2px) rotate(108deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(144deg);
    }
    50% {
        transform: translate(-1px, 3px) rotate(180deg);
    }
    60% {
        transform: translate(-1px, 1px) rotate(216deg);
    }
    70% {
        transform: translate(3px, 1px) rotate(252deg);
    }
    80% {
        transform: translate(-2px, -1px) rotate(288deg);
    }
    90% {
        transform: translate(2px, 1px) rotate(324deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(360deg);
    }
}

@keyframes speed {
    0% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    10% {
        transform: translate(-1px, -3px) rotate(-1deg);
    }
    20% {
        transform: translate(-2px, 0px) rotate(1deg);
    }
    30% {
        transform: translate(1px, 2px) rotate(0deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }
    50% {
        transform: translate(-1px, 3px) rotate(-1deg);
    }
    60% {
        transform: translate(-1px, 1px) rotate(0deg);
    }
    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }
    80% {
        transform: translate(-2px, -1px) rotate(1deg);
    }
    90% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}

@keyframes strikes {
    from {
        left: 25px;
    }
    to {
        left: -80px;
        opacity: 0;
    }
}

.apps_wrapper .preloader {
    position: fixed;
    inset: 0;
    background-color: var(--color__white);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0 1rem;
}

.apps_wrapper .car {
    position: relative;
    width: 100px;
    height: 55px;
}

.apps_wrapper .car .strike {
    position: absolute;
    width: 11px;
    height: 1px;
    background: var(--color__primary);
    animation: strikes 0.2s linear infinite;
}

.apps_wrapper .car .strike2 {
    top: 11px;
    animation-delay: 0.05s;
}

.apps_wrapper .car .strike3 {
    top: 22px;
    animation-delay: 0.1s;
}

.apps_wrapper .car .strike4 {
    top: 33px;
    animation-delay: 0.15s;
}

.apps_wrapper .car .strike5 {
    top: 44px;
    animation-delay: 0.2s;
}

.apps_wrapper .car-detail {
    position: absolute;
    display: block;
    animation: speed 0.3s linear infinite;
}

.apps_wrapper .car-detail.wheel {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    top: 28px;
    left: 13px;
    border: 3px solid var(--color__white);
    background: linear-gradient(45deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(-45deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(90deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(0deg, transparent 45%, #000 46%, #000 54%, transparent 55%), radial-gradient(#000 29%, transparent 30%, transparent 50%, #000 51%), var(--color__white);
    animation-name: spin;
}

.apps_wrapper .car-detail.wheel2 {
    left: 63px;
}


/*--------------------------------------------------------------
  # Home Page
  --------------------------------------------------------------*/

.apps_wrapper {
    height: 100vh;
}

.apps_wrapper .container {
    overflow: inherit;
    overflow-x: hidden;
    height: 100%;
    padding: 0px !important;
}

.apps_wrapper .container {
    max-width: 76.7rem;
    margin: 0 auto;
    background-color: #F5F5F5;
    position: relative;
    overflow: hidden;
}

.apps_wrapper header:not(.shopping-header) {
    background-color: #18272C;
    padding: 1.5rem;
}

.apps_wrapper .shopping-header {
    background-color: #0083A1;
    padding: 1.5rem;
}

.apps_wrapper header .top_nav {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    align-items: center;
}

.apps_wrapper header .logo {
    flex-shrink: 0;
}

.apps_wrapper header .logo img {
    max-width: 90%;
}

.apps_wrapper .shopping-header .sidebarToggler img {
    filter: grayscale(100) contrast(100);
}

.apps_wrapper header:not(.shopping-header) .cart-count {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: 600;
    font-size: 2rem;
    background-color: #0083A1;
    margin-right: 1.8rem;
}

.apps_wrapper .shopping-header .cart-count {
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: 600;
    font-size: 2rem;
    background-color: #000;
    margin-right: 1.8rem;
}

.apps_wrapper header .hamburger {
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.apps_wrapper header .hamburger button {
    cursor: pointer;
}

.apps_wrapper header .breadcrumb ul:not(.submenu) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0;
}

.apps_wrapper header .breadcrumb ul:not(.submenu) li+li::before {
    content: ">";
    color: #fff;
    display: inline-block;
    margin: 0 1rem;
    font-size: 2.8rem;
}

@media screen and (min-width: 576px) {
    .apps_wrapper header .breadcrumb ul:not(.submenu) li+li::before {
        font-size: 2.4rem;
    }
}

.apps_wrapper header .breadcrumb ul:not(.submenu) li a {
    font-size: 2.8rem;
    color: #fff;
}

@media screen and (min-width: 576px) {
    .apps_wrapper header .breadcrumb ul:not(.submenu) li a {
        font-size: 2.4rem;
    }
}

.apps_wrapper header .breadcrumb ul:not(.submenu) li.item-has-submenu {
    position: relative;
}

.apps_wrapper header .breadcrumb ul:not(.submenu) li.item-has-submenu>a::after {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 5px 0 5px;
    border-color: #fff transparent transparent transparent;
    margin-top: -1px;
    margin-left: 2px;
