    padding-bottom: 0.3rem;
    transition: all 0.25s;
    opacity: 0;
    visibility: hidden;
}

.apps_wrapper header .breadcrumb ul:not(.submenu) li.item-has-submenu .submenu.active {
    opacity: 1;
    visibility: visible;
    height: 100vh;
    overflow-y: scroll;
    position: fixed;
