.apps_wrapper header .items-filter {
    text-align: center;
    margin: 2rem 0 1rem;
}

.apps_wrapper header .items-filter ul {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
    flex-wrap: wrap;
}

@media (min-width: 375px) {
    .apps_wrapper header .items-filter ul {
        gap: 1rem;
    }
}

@media (min-width: 410px) {
    .apps_wrapper header .items-filter ul {
        gap: 1.5rem;
    }
}

.apps_wrapper header .items-filter ul li {
    font-weight: 500;
}

.apps_wrapper header .items-filter ul li label {
    font-size: 1.3rem;
    position: relative;
    display: block;
    font-weight: 400;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    user-select: none;
    color: #fff;
    padding-left: 2.8rem;
}

@media (min-width: 375px) {
    .apps_wrapper header .items-filter ul li label {
        font-size: 1.5rem;
        padding-left: 3rem;
    }
}

@media (min-width: 410px) {
    .apps_wrapper header .items-filter ul li label {
        font-size: 1.5rem;
        padding-left: 3.2rem;
    }
}

.apps_wrapper header .items-filter ul li label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    box-shadow: inset 0 0 0 1.5rem var(--fill);
    border: 0.1rem solid #fff;
}

@media (min-width: 410px) {
    .apps_wrapper header .items-filter ul li label::before {
        width: 2.4rem;
        height: 2.4rem;
    }
}

.apps_wrapper header .items-filter ul li label::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0.5rem;
    width: 1.4rem;
