/*--------------------------------------------------------------
>>> TABLE OF CONTENTS:
----------------------------------------------------------------*/

:root {
    --color__white_smoke: #F5F5F5;
    --color__text: #17272B;
    /*17272b*/
    --color__100grammes: #17272b;
    /*a52513*/
    --color__100grammes_alt: #5CAB93;
    --color__100grammes_dim: #c0c0c0;
    --color__100grammes_superdim: #e3e3e3;
    --color__100grammes_dimtxt: #616161;
    --color__iga: #f99200;
    --color__maxi: #05ab63;
    --color__metro: #c20505;
    --color__provigo: #01446f;
    --color__superc: #6dc5fd;
    --font-pacifico: "Pacifico", cursive;
    --font-systemui: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}


/* Test just for fun */

html {
    font-size: 58%;
    -webkit-text-size-adjust: 100%;
}

@media screen and (min-width: 768px) {
    html {
        font-size: 60%;
    }
}

@media screen and (min-width: 992px) {
    html {
        font-size: 62.5%;
    }
}


/* width */

::-webkit-scrollbar {
    width: 5px;
}


/* Track */

::-webkit-scrollbar-track {
    background: #f1f1f1;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: var(--color__100grammes_alt);
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

body {
    font-size: 1.3rem!important;
    box-sizing: border-box!important;
    margin: 0!important;
    padding: 0!important;
    font-family: var(--font-systemui)!important;
    color: var(--color__text)!important;
    background-color: #ededed!important;
    overflow-y: hidden!important;
    line-height: normal!important;
}

.dashboard-wrapper p {
    font-size: 1.3rem!important;
    line-height: 1.5!important;
    margin: 0!important;
    background-color: transparent!important;
}

.dashboard-wrapper p::first-letter {
    text-transform: capitalize;
}

.dashboard-wrapper img {
    max-width: 80%!important;
    height: auto!important;
}

.dashboard-wrapper img.product-img {
    max-width: 5rem;
}

.dashboard-wrapper ul,
.dashboard-wrapper ol {
    list-style: none!important;
    margin: 0!important;
    padding: 0!important;
}

.dashboard-wrapper ul li,
.dashboard-wrapper ol li {
    font-size: 1.3rem!important;
}

.dashboard-wrapper a {
    color: inherit;
    text-decoration: none;
    font-size: 1.3rem;
}

.dashboard-wrapper a:hover {
    color: inherit;
    text-decoration: none;
}

.dashboard-wrapper h1,
.dashboard-wrapper h2,
.dashboard-wrapper h3,
.dashboard-wrapper h4,
.dashboard-wrapper h5,
.dashboard-wrapper h6 {
    margin: 0!important;
    padding: 0!important;
}

.dashboard-wrapper h3 {
    font-size: 2rem!important;
    font-weight: 400!important;
    /*text-transform: uppercase;*/
}


/*-------------------------------------------------
    [ ## Fields ]
---------------------------------------------------*/

.dashboard-wrapper input[type=text],
.dashboard-wrapper input[type=email],
.dashboard-wrapper input[type=url],
.dashboard-wrapper input[type=password],
.dashboard-wrapper input[type=number],
.dashboard-wrapper input[type=search] {
    padding: 1rem 1.5rem;
    font-size: 1.2rem;
    font-family: var(--font-systemui);
}

.dashboard-wrapper input[type=text]:focus,
.dashboard-wrapper input[type=email]:focus,
.dashboard-wrapper input[type=url]:focus,
.dashboard-wrapper input[type=password]:focus,
.dashboard-wrapper input[type=number]:focus,
.dashboard-wrapper input[type=search]:focus {
    outline: none;
    box-shadow: none;
    border-color: #ced4da;
}

.dashboard-wrapper input[type=time]::-webkit-inner-spin-button,
.dashboard-wrapper input[type=time]::-webkit-outer-spin-button,
.dashboard-wrapper input[type=date]::-webkit-inner-spin-button,
.dashboard-wrapper input[type=date]::-webkit-outer-spin-button,
.dashboard-wrapper input[type=number]::-webkit-inner-spin-button,
.dashboard-wrapper input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.dashboard-wrapper input[type=search] {
    -webkit-appearance: textfield;
    appearance: none;
}

.dashboard-wrapper input[type=search]::-webkit-search-decoration {
    -webkit-appearance: none;
}

.dashboard-wrapper select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    font-family: var(--font-systemui);
    padding: 1rem 1.5rem;
    font-size: 1.3rem;
}

input:-webkit-autofill,
.dashboard-wrapper textarea:-webkit-autofill,
.dashboard-wrapper select:-webkit-autofill {
    box-shadow: 0 0 0px 0 rgba(var(--color__text), 0) inset;
    -webkit-box-shadow: 0 0 0px 0 rgba(var(--color__text), 0) inset;
    -webkit-text-fill-color: var(--color__text) !important;
}

input:-webkit-autofill:focus {
    box-shadow: 0 0 0px 0 rgba(var(--color__text), 0) inset;
    -webkit-box-shadow: 0 0 0px 0 rgba(var(--color__text), 0) inset;
    -webkit-text-fill-color: var(--color__text) !important;
}

.dashboard-wrapper textarea.form-control {
    display: block;
    width: 100%;
    display: block;
    min-height: 150px;
    font-family: var(--font-systemui);
    padding: 1rem 1.5rem;
    font-size: 1.3rem;
}

.dashboard-wrapper textarea.form-control:focus {
    outline: none;
    box-shadow: none;
}

.dashboard-wrapper .form-floating>.form-control {
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0;
}

.dashboard-wrapper ::-webkit-input-placeholder {
    color: var(--color__text);
}

.dashboard-wrapper .widget_newsletter::-webkit-input-placeholder {
    color: #808080;
}

:-moz-placeholder {
    color: var(--color__text);
    opacity: 1;
}

::-moz-placeholder {
    color: var(--color__text);
    opacity: 1;
}

:-ms-input-placeholder {
    color: var(--color__text);
}

.dashboard-wrapper .text-red {
    color: red !important;
    font-size: 1.5rem !important;
}

.dashboard-wrapper .text-center {
    text-align: center;
}

.dashboard-wrapper .breadcrumb {
    justify-content: center;
    margin: 0;
}

.dashboard-wrapper .breadcrumb .breadcrumb-item a {
    color: var(--color__white_smoke);
    font-size: 1rem;
}

@media screen and (min-width: 576px) {
    .dashboard-wrapper .breadcrumb .breadcrumb-item a {
        font-size: 1.2rem;
    }
}

@media screen and (min-width: 768px) {
    .dashboard-wrapper .breadcrumb .breadcrumb-item a {
        font-size: 1.5rem;
    }
}

.dashboard-wrapper .breadcrumb .breadcrumb-item+.breadcrumb-item::before {
    content: "-";
    color: var(--color__white_smoke);
    position: relative;
    top: 6px;
}

.dashboard-wrapper .topbutton {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    background-color: var(--color__white_smoke);
    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
    font-size: 2rem;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    border-radius: 0.5rem;
    opacity: 0;
    visibility: hidden;
    z-index: 9999;
}

.dashboard-wrapper .topbutton:hover {
    color: var(--color__white_smoke);
    background-color: var(--color__primary);
}

.dashboard-wrapper .topbutton.btn-show {
    visibility: visible;
    opacity: 1;
    bottom: 4rem;
    right: 4rem;
}

.dashboard-wrapper .btn-theme {
    background-color: var(--color__primary);
    color: var(--color__white_smoke);
    border-radius: 0.8rem;
    padding: 1.5rem 3rem;
    font-size: 1.3rem;
    font-weight: 600;
    transition: all 0.3s;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.dashboard-wrapper .btn-theme:hover {
    color: var(--color__white_smoke);
}

.dashboard-wrapper .btn-rounded-white {
    background-color: var(--color__white_smoke);
    color: var(--color__primary);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
    text-align: center;
}

.dashboard-wrapper .btn-theme-primary {
    background-color: var(--color__primary);
    color: var(--color__white_smoke);
    border-radius: 0.4rem;
    padding: 1.5rem 3rem;
    display: inline-block;
    transition: all 0.3s;
}

.dashboard-wrapper .btn-theme-primary:hover {
    color: var(--color__white_smoke);
}

.dashboard-wrapper .btn-rounded-primary {
    background-color: var(--color__primary);
    color: var(--color__white_smoke);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
    text-align: center;
}

.dashboard-wrapper .btn-rounded-primary:hover {
    color: var(--color__white_smoke);
}

.dashboard-wrapper .btn-rounded-primary-outline {
    background-color: transparent;
    color: var(--color__primary);
    border: 0.1rem solid var(--color__primary);
    border-radius: 3rem;
    padding: 1.2rem 3.5rem;
    transition: all 0.3s;
    display: inline-block;
}

.dashboard-wrapper .btn-rounded-primary-outline svg,
.dashboard-wrapper .btn-rounded-primary-outline svg path {
    transition: all 0.3s;
}

.dashboard-wrapper .btn-rounded-primary-outline:hover {
    background-color: var(--color__primary);
    color: var(--color__white_smoke);
}

.dashboard-wrapper .btn-rounded-primary-outline:hover svg path {
    fill: #fff;
    stroke: #fff;
}

.dashboard-wrapper .btn-circle-white {
    background-color: var(--color__white_smoke);
    color: var(--color__primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4.5rem;
    height: 4.5rem;
    transition: all 0.3s;
}

.dashboard-wrapper .btn-circle-primary {
    background-color: var(--color__primary);
    color: var(--color__white_smoke);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4.5rem;
    height: 4.5rem;
    transition: all 0.3s;
}

@media screen and (min-width: 576px) {
    .modal.user_agreement .modal-dialog {
        min-width: 60rem;
    }
}

.modal.user_agreement .modal-body {
    padding: 5rem 4rem 2rem 5rem;
}

@media screen and (max-width: 575px) {
    .modal.user_agreement .modal-body {
        padding: 3rem 2rem 2rem 2rem;
    }
}

.modal.user_agreement .modal-footer {
    border: 0;
    padding: 1rem 5rem 5rem;
}

@media screen and (max-width: 575px) {
    .modal.user_agreement .modal-footer {
        padding: 1rem 2rem 3rem;
    }
}

.modal.user_agreement .modal-footer button {
    font-size: 1.6rem;
    padding: 1rem 3rem;
    margin: 0 1rem;
}

.modal.user_agreement .modal-title {
    margin-bottom: 1rem;
    color: var(--color__primary);
    font-size: 2rem;
}

.modal.user_agreement .agreements_details {
    max-height: 35rem;
    overflow-y: scroll;
    padding-right: 1rem;
}

.modal.user_agreement .agreements_details::-webkit-scrollbar {
    width: 0.5rem;
    background-color: #EEEEEE;
}

.modal.user_agreement .agreements_details::-webkit-scrollbar-thumb {
    background-color: var(--color__primary);
}

.modal.user_agreement .agreements_details ol {
    counter-reset: cupcake;
}

.modal.user_agreement .agreements_details ol li {
    counter-increment: cupcake;
    padding-left: 2rem;
}

.modal.user_agreement .agreements_details ol li:before {
    content: counters(cupcake, ". ") ". ";
    margin-left: -1.8rem;
}

.modal.user_agreement .agreements_details ol li+li {
    margin-top: 2rem;
}

@media screen and (min-width: 576px) {
    .modal.update-password .modal-dialog {
        min-width: 60rem;
    }
}

.modal.update-password .btn-close {
    width: 4rem;
    height: 4rem;
    border: 0.1rem solid var(--color__theme__black);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color__theme__black);
    opacity: 1;
    padding: 0;
    position: absolute;
    right: -2rem;
    top: -2rem;
    background-color: var(--color__white_smoke);
    font-size: 1.2rem;
    outline: 0;
    box-shadow: none;
    z-index: 9;
}

.modal.update-password .modal-content {
    border-radius: 1rem;
}

.modal.update-password .modal-body {
    padding: 5rem;
}

.modal.update-password .modal-title {
    margin: 1rem 0;
    color: var(--color__theme__black);
    font-size: 3rem;
    font-weight: bold;
}

.modal.recharge_success .modal-content {
    border-radius: 1rem;
}

.modal.recharge_success .modal-body {
    padding: 6rem 4rem;
}

.modal.recharge_success h6 {
    font-size: 2rem;
}

.modal.recharge_success h3 {
    font-size: 4rem;
}

.dashboard-wrapper .tooltip {
    --bs-tooltip-arrow-width: 1.3rem;
    --bs-tooltip-arrow-height: .8rem;
}

.dashboard-wrapper .tooltip .tooltip-inner {
    font-size: 1.3rem;
    padding: 0.5rem;
}

@keyframes spin {
    0% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    10% {
        transform: translate(-1px, -3px) rotate(36deg);
    }
    20% {
        transform: translate(-2px, 0px) rotate(72deg);
    }
    30% {
        transform: translate(1px, 2px) rotate(108deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(144deg);
    }
    50% {
        transform: translate(-1px, 3px) rotate(180deg);
    }
    60% {
        transform: translate(-1px, 1px) rotate(216deg);
    }
    70% {
        transform: translate(3px, 1px) rotate(252deg);
    }
    80% {
        transform: translate(-2px, -1px) rotate(288deg);
    }
    90% {
        transform: translate(2px, 1px) rotate(324deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(360deg);
    }
}

@keyframes speed {
    0% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    10% {
        transform: translate(-1px, -3px) rotate(-1deg);
    }
    20% {
        transform: translate(-2px, 0px) rotate(1deg);
    }
    30% {
        transform: translate(1px, 2px) rotate(0deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }
    50% {
        transform: translate(-1px, 3px) rotate(-1deg);
    }
    60% {
        transform: translate(-1px, 1px) rotate(0deg);
    }
    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }
    80% {
        transform: translate(-2px, -1px) rotate(1deg);
    }
    90% {
        transform: translate(2px, 1px) rotate(0deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}

@keyframes strikes {
    from {
        left: 25px;
    }
    to {
        left: -80px;
        opacity: 0;
    }
}

.preloader {
    position: fixed;
    inset: 0;
    background-color: var(--color__white_smoke);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 0 1rem;
}

.car {
    position: relative;
    width: 100px;
    height: 55px;
}

.car .strike {
    position: absolute;
    width: 11px;
    height: 1px;
    background: var(--color__primary);
    animation: strikes 0.2s linear infinite;
}

.car .strike2 {
    top: 11px;
    animation-delay: 0.05s;
}

.car .strike3 {
    top: 22px;
    animation-delay: 0.1s;
}

.car .strike4 {
    top: 33px;
    animation-delay: 0.15s;
}

.car .strike5 {
    top: 44px;
    animation-delay: 0.2s;
}

.car-detail {
    position: absolute;
    display: block;
    animation: speed 0.3s linear infinite;
}

.car-detail.wheel {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    top: 28px;
    left: 13px;
    border: 3px solid var(--color__white_smoke);
    background: linear-gradient(45deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(-45deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(90deg, transparent 45%, #000 46%, #000 54%, transparent 55%), linear-gradient(0deg, transparent 45%, #000 46%, #000 54%, transparent 55%), radial-gradient(#000 29%, transparent 30%, transparent 50%, #000 51%), var(--color__white_smoke);
    animation-name: spin;
}

.car-detail.wheel2 {
    left: 63px;
}


/*--------------------------------------------------------------
# Home Page
--------------------------------------------------------------*/

.dashboard-wrapper {
    background-color: var(--color__white_smoke);
}

.dashboard-wrapper header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #17272b;
    padding: 1rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1024;
    box-shadow: 0px 2px 10px 0px #e0dede;
}

.dashboard-wrapper header .date-time {
    color: #FFF;
}

header#header.sticky-header {
    background: var(--color__text);
}

header#header .brand img {
    max-width: 60%;
}

.dashboard-wrapper header #logo-white {
    display: none;
}

.dashboard-wrapper header.sticky-header #logo-white {
    display: block;
}

.dashboard-wrapper header.sticky-header #logo-zomp {
    display: none !important;
}

#logo-white,
#logo-zomp {
    width: 240px;
}

.sad-component img {
    width: 200px;
}

.dashboard-wrapper header.sticky-header .date-time {
    color: var(--color__white_smoke);
}

.dashboard-wrapper header.sticky-header path {
    color: var(--color__white_smoke) !important;
    fill: var(--color__white_smoke) !important;
    background: var(--color__white_smoke) !important;
}

.dashboard-wrapper header.sticky-header .right-action ul .cart .amount {
    color: var(--color__text);
}

.dashboard-wrapper header.sticky-header .right-action .author-avatar {
    background: var(--color__white_smoke);
    color: var(--color__text);
}

.dashboard-wrapper header .date-time .date {
    font-weight: 500;
    font-size: 1.5rem;
    text-transform: capitalize;
}

.dashboard-wrapper header .date-time .period {
    font-weight: 400;
    font-size: 1.2rem;
}

.dashboard-wrapper header .brand {
    padding-top: 1rem;
}

.dashboard-wrapper header .brand a {
    display: inline-block;
    max-width: 300px;
}

.dashboard-wrapper header .right-action ul {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.dashboard-wrapper header .right-action ul .cart {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dashboard-wrapper header .right-action ul .cart .amount {
    position: absolute;
    top: 6px;
    left: 18px;
    font-size: 1.3rem;
    color: var(--color__white_smoke);
    font-weight: 600;
}

.dashboard-wrapper header .right-action .author-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color__text);
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    color: var(--color__white_smoke);
    font-weight: 500;
    cursor: pointer;
}

.dashboard-wrapper .dashboard-content {
    display: flex;
    align-items: stretch;
    padding-top: 8rem;
    padding-bottom: 0rem;
    overflow: hidden;
    background-color: #ededed;
}

.dashboard-wrapper .left-sidebar {
    padding: 0rem 0.5rem 0rem 1rem;
    width: 13vw;
    bottom: 0rem;
    top: 8.6rem;
    overflow-y: auto;
    margin-top: 2.9rem;
}

.dashboard-wrapper .left-sidebar .collapse-item .collapse-header {
    font-size: 1.5rem;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
    padding: 0.15rem 0;
    color: rgba(var(--color__text), 0);
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre label {
    color: #616161;
}

.dashboard-wrapper .left-sidebar .collapse-item .collapse-header svg {
    margin-right: 0.2rem;
    padding-left: 0.3rem;
    margin-bottom: 0.2rem;
    vertical-align: middle;
}

.dashboard-wrapper .left-sidebar .collapse-item .collapse-header .icon {
    transition: all 0.2s;
    display: inline-block;
}

.dashboard-wrapper .left-sidebar .collapse-item .collapse-header.active span {
    transform: rotate(90deg);
}

.dashboard-wrapper .left-sidebar .collapse-item .collapse-body {
    padding: 0.4rem 0;
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre input[type=radio] {
    display: none;
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre label {
    font-size: 1.3rem;
    padding: 0.5rem 0 0.5rem 3.4rem;
    position: relative;
    display: block;
    font-weight: 400;
    transition: all 0.1s ease-in-out;
    cursor: pointer;
    user-select: none;
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre label::before {
    content: "";
    position: absolute;
    left: 1.2rem;
    top: 50%;
    transform: translateY(-50%);
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
    border: 0.14rem solid var(--color__text);
    background-color: var(--color__white_smoke);
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre input:checked+label,
.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre label:hover {
    background-color: #6d6d6d;
    color: #fff;
    border-radius: 2px;
}

.dashboard-wrapper .left-sidebar .collapse-item .input-group-membre input:checked+label::before {
    box-shadow: inset 0 0 0 0.4rem #000;
}

.dashboard-wrapper .left-sidebar .collapse-item+.collapse-item {
    margin-top: 0.5rem;
}

.dashboard-wrapper .left-sidebar .reaction a {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1.5rem;
