.profile-modal .header {
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e9e9e9;
    color: #495057;
}

.profile-modal .body {
    padding-top: 15px;
}

.profile-modal .body textarea {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 1.4rem;
    line-height: 1.5;
    min-height: 10rem;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    overflow: auto;
    resize: vertical;
}

.profile-modal .rodal-cancel-btn,
.profile-modal .rodal-confirm-btn {
    position: absolute;
    font: inherit;
    bottom: 16px;
    width: 70px;
    padding: 4px 15px;
    border-radius: 3px;
    transition: background 0.2s;
    border: 1px solid var(--color__100grammes);
}

.profile-modal .rodal-confirm-btn {
    color: #fff;
    right: 20px;
    background: var(--color__100grammes);
    cursor: pointer;
    width: 15rem;
    height: 4rem;
    border-radius: 45px;
}

.profile-modal .rodal-confirm-btn:disabled {
    pointer-events: none;
    cursor: default;
    opacity: 0.6;
}

.profile-modal .rodal-confirm-btn:hover {
    background: var(--color__100grammes);
}

.profile-modal .rodal-cancel-btn {
    color: var(--color__100grammes);
    right: 16px;
    background: #fff;
    cursor: pointer;
}

.profile-modal .rodal-cancel-btn:hover {
    background: #fafafa;
}

.profile-modal .rodal-cancel-btn:focus,
.profile-modal .rodal-confirm-btn:focus {
    outline: none;
}

.profile-modal .rodal-confirm-btn:active {
    background: var(--color__100grammes);
}

.profile-modal .rodal-cancel-btn:active {
    background: #fafafa;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px transparent;
}

.profile-modal span {
    color: #495057;
}

.profile-modal h3 {
    font-size: 1.8rem;
    font-weight: 500;
    color: #495057;
    text-transform: none
}

.profile-modal label {
    font-weight: 500;
    color: #495057;
}

.profile-modal {
    font-size: 1.2rem;
}

* {
    box-sizing: border-box;
}

.profile-modal .column-first {
    float: left;
    width: 20%;
    padding: 1.5rem;
    /* Should be removed. Only for demonstration */
}

.profile-modal .column-second {
    float: left;
    width: 80%;
    padding: 0.5rem;
}

.profile-modal .column-second svg {
    vertical-align: sub;
    cursor: pointer;
}


/* Clear floats after the columns */

.profile-modal .row:after {
    content: "";
    display: table;
    clear: both;
}

.profile-modal input[type=text],
.profile-modal input[type=password],
.profile-modal input[type=date],
.profile-modal input[type=datetime],
.profile-modal input[type=number],
.profile-modal input[type=search],
.profile-modal input[type=time],
.profile-modal input[type=url],
.profile-modal input[type=email],
textarea,
select {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    border: 1px solid #BEBEBE;
    padding: 1.5rem;
    margin: 0px;
    -webkit-transition: all 0.30s ease-in-out;
    -moz-transition: all 0.30s ease-in-out;
    -ms-transition: all 0.30s ease-in-out;
    -o-transition: all 0.30s ease-in-out;
    transition: all 0.30s ease-in-out;
    outline: none;
}

.profile-modal input[type=text]:focus,
.profile-modal input[type=date]:focus,
.profile-modal input[type=datetime]:focus,
.profile-modal input[type=number]:focus,
.profile-modal input[type=search]:focus,
.profile-modal input[type=time]:focus,
.profile-modal input[type=url]:focus,
.profile-modal input[type=email]:focus,
.profile-modal textarea:focus,
.profile-modal select:focus {
    -moz-box-shadow: 0 0 8px #88D5E9;
    -webkit-box-shadow: 0 0 8px #88D5E9;
    box-shadow: 0 0 8px #88D5E9;
    border: 1px solid #88D5E9;
}

.profile-modal .field-divided {
    width: 40%;
    height: 4rem;
    border-radius: 45px;
}

.profile-modal .field-long {
    width: 100%;
}

.profile-modal .field-select {
    width: 100%;
}

.profile-modal .field-textarea {
    height: 100px;
}

.profile-modal input[type=submit],
.profile-modal input[type=button] {
    background: var(--color__100grammes);
    padding: 8px 15px 8px 15px;
    border: none;
    color: #fff;
    cursor: pointer;
}

.profile-modal input[type=submit]:hover,
.profile-modal input[type=button]:hover {
    background: var(--color__100grammes);
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
}

.profile-modal .required {
    color: red;
}